.profile {
  box-sizing: border-box;
  width: 370px;
  margin-left: 20px;

  &__top {
    position: relative;
    padding: 10px;
    background-color: #ffffff;
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 40px;
    background: url('../../../../images/profile/background-picture.jpg') no-repeat top;
  }

  &__name {
    margin-top: 15px;
    margin-bottom: 10px;
    font-size: 24px;
    font-weight: 600;
    color: #47505c;
  }

  &__status {
    margin-bottom: 12px;
    color: #b5b5b5;
  }

  &__pencil {
    position: absolute;
    display: flex;
    justify-content: center;
    width: 38px;
    height: 38px;
    right: 0;
    top: 0;
    border: 1px solid rgb(230, 234, 238);
    border-radius: 20px;
    background-color: rgb(245, 248, 250);
    transform: translate(25%, -25%);
    transition: 0.3s;

    &:hover {
      box-shadow: 0px 0px 10px 1px #b5b5b5;
      transform: translate(25%, -25%) scale(1.2);
    }
  }

  &__info {
    margin: 20px;
    border-top: 3px rgb(222, 231, 239) solid;
    font-size: 16px;

    &-item {
      margin-top: 10px;
      line-height: 1.3;
      color: #56688a;

      & >:first-child {
        font-size: 12px;
        color: #818ea7;
        margin: 15px 0 0;
      }
    }
  }

  &__contact {
    font-weight: 600;
  }

  &__money {
    display: flex;
    align-items: center;
    font-weight: bold;

    &-bar {
      position: relative;
      width: 120px;
      height: 4px;
      margin-right: 12px;
      background-color: #dfe4ee;

      &::before {
        position: absolute;
        content: "";
        width: 70%;
        height: 4px;
        background-color: #2da0f8;
      }
    }

    &-limit span {
      color: #2da0f8;
    }
  }

  &__biography {
    font-size: 14px;
  }

  &__skills {
    display: flex;
    margin-top: 10px;
  }

  &__skill {
    padding: 6px 8px 5px;
    border-radius: 3px;
    font-size: 10px;
    text-transform: uppercase;
    color: #fff;
    transition: transform 0.3s;

    &:not(:last-child) {
      margin-right: 6px;
    }

    &:nth-child(1) {
      background-color: #0474c8;
    }

    &:nth-child(2) {
      background-color: #45b854;
    }

    &:nth-child(3) {
      background-color: #8191a6;
    }

    &:nth-child(4) {
      background-color: #f86b67;
    }
    
    &:hover {
      transform: scale(1.2);
    }
  }

  &__bottom {
    margin-top: 22px;
    padding: 30px;
    background-color: #ffffff;
  }
}


